@charset "UTF-8";
/* reCAPTCHAバッジを非表示 */
.grecaptcha-badge {
  visibility: hidden;
}

body {
  color: #212121;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
  overflow-x: hidden;
  width: 100vw;
}

body *::-webkit-scrollbar:horizontal {
  display: none;
}

html[lang=en-US] body {
  font-family: "Arial", sans-serif;
}

main {
  overflow: hidden;
  position: relative;
}

* {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
}

picture {
  display: block;
}

img, video {
  display: block;
  width: 100%;
  height: auto;
}

/* header */
.header_space {
  aspect-ratio: 750/100;
  display: block;
  width: 100%;
}

.header {
  position: relative;
  z-index: 100;
}

.header_container {
  background-color: #fff;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}

.header_wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
  width: 90%;
}

.header_logo {
  width: 15%;
}

.header_menuTop {
  display: flex;
  align-items: center;
  gap: 1vw;
}

.header_menuTop_link,
.header_menuBottom_link {
  font-size: min(1.2vw, 26px);
}

.header_menuBottom {
  display: flex;
  align-items: center;
  gap: 1vw;
  margin-top: 4%;
}

/* パンくず */
.breadcrumbs_wrap {
  font-family: "Inter", sans-serif;
  font-size: 2.4vw;
  font-weight: 400;
  line-height: 1.5;
  margin-left: auto;
  margin-right: auto;
  margin-top: 5%;
  width: 90%;
}

.breadcrumbs_wrap.-float {
  color: #fff;
  margin-top: 0;
  position: absolute;
  top: 4.6vw;
  left: 50%;
  transform: translateX(-50%);
}

/* /page-templates/page-title.php */
.page_title_wrap {
  background-size: cover;
  background-position: center;
  padding-top: 14%;
  padding-bottom: 15%;
}

.page_title_en {
  --scaleX: 0;
  color: #fff;
  font-size: 5.3vw;
  font-family: "Inknut Antiqua", serif;
  font-weight: 700;
  line-height: 1.6;
  text-align: center;
}

.page_title_en::after {
  content: "";
  aspect-ratio: 520/4;
  background-color: #fff;
  display: block;
  margin: 1.6vw auto 0.9vw;
  width: 70vw;
  transform: scaleX(var(--scaleX));
}

.page_title_ja {
  color: #fff;
  font-size: 6.4vw;
  font-weight: 700;
  line-height: 1.6;
  text-align: center;
}

.page_title_en.-green,
.page_title_ja.-green {
  color: #3A7961;
}

.page_title_en.-green::after {
  background-color: #3A7961;
}

/* /page-templates/headline-bg.php */
.headline_bg {
  background-color: #3A7961;
  color: #fff;
  font-size: 6.4vw;
  font-weight: 600;
  line-height: 1.5;
  padding: 5% 3%;
  text-align: center;
}

/* /page-templates/headline-underline.php */
.headline_underline {
  font-size: 5.3vw;
  font-weight: 700;
  line-height: 1.8;
  letter-spacing: 0.07em;
}

.headline_underline::after {
  content: "";
  background-color: #3A7961;
  display: block;
  margin-top: 2vw;
  width: 100%;
  height: 1.1vw;
}

main[data-lang=en_US] .headline_underline {
  letter-spacing: 0.04em;
}

/* /page-templates/roundFull-btn.php */
.roundFull_btn {
  border: 0.6vw solid #000;
  border-radius: 1000px;
  display: block;
  font-size: 4.2vw;
  font-weight: 500;
  text-align: center;
  padding: 3vw 8vw;
  width: 100%;
  position: relative;
}

.roundFull_btn.outlineGreen {
  background-color: #fff;
  border-color: #3A7961;
  color: #3A7961;
}

.roundFull_btn.outlineWhite {
  border-color: #fff;
  color: #fff;
}

.roundFull_btn.outlineGreen::after,
.roundFull_btn.outlineWhite::after {
  content: "";
  aspect-ratio: 15/18;
  background-size: cover;
  display: block;
  width: 2vw;
  position: absolute;
  top: 50%;
  right: 5vw;
  transform: translateY(-50%);
}

.roundFull_btn.outlineGreen::after {
  background-image: url(../images/common/arrow_right_green.svg);
}

.roundFull_btn.outlineWhite::after {
  background-image: url(../images/common/arrow_right_white.svg);
}

/* /page-templates/round-btn.php */
.round_btn {
  border: 0.6vw solid #000;
  border-radius: 2.6vw;
  display: block;
  font-size: 4.2vw;
  font-weight: 500;
  text-align: center;
  padding: 5vw 7vw;
  width: 100%;
  position: relative;
}

.round_btn::after {
  content: "";
  aspect-ratio: 15/17;
  background-image: url(../images/common/arrow_right_white.svg);
  background-size: cover;
  display: block;
  width: 2.4vw;
  position: absolute;
  top: 50%;
  right: 6.5vw;
  transform: translateY(-50%);
}

.round_btn.outlineGreen {
  border-color: #3A7961;
  color: #3A7961;
}

.round_btn.outlineGreen::after {
  background-image: url(../images/common/arrow_right_green.svg);
}

.round_btn.outlineWhite {
  border-color: #fff;
  color: #fff;
}

.round_btn.bgWhite {
  background-color: #fff;
  border-color: #3A7961;
  color: #3A7961;
  font-weight: 700;
}

.round_btn.bgWhite::after {
  background-image: url(../images/common/arrow_right_green.svg);
}

/* /page-templates/round-btn-arrow.php */
.round_btn_arrow {
  border: 0.6vw solid #000;
  border-radius: 2.6vw;
  display: block;
  font-size: 4.2vw;
  font-weight: 700;
  line-height: 1.6;
  text-align: center;
  padding: 3vw 11vw;
  width: 100%;
  position: relative;
}

.round_btn_arrow::after {
  content: "";
  aspect-ratio: 1/1;
  background-image: url(../images/common/arrowCircle_right_green.svg);
  background-size: cover;
  display: block;
  width: 6vw;
  position: absolute;
  top: 50%;
  right: 4vw;
  transform: translateY(-50%);
}

/* /page-templates/product-item.php */
.productItem_box {
  border: 0.6vw solid #3A7961;
  border-radius: 4vw;
  display: block;
  overflow: hidden;
}

.productItem_info {
  padding: 9% 6%;
}

.productItem_title {
  font-size: 5.3vw;
  font-weight: 600;
  line-height: 1.5;
}

.productItem_title::after {
  content: "";
  aspect-ratio: 590/4;
  background-color: #3A7961;
  border-radius: 1000px;
  display: block;
  margin-top: 8%;
}

.productItem_desc {
  margin-top: 5%;
}

.productItem_desc_text {
  font-size: 3.7vw;
  line-height: 1.5;
  margin-top: 2%;
  text-align: justify;
}

.productItem_desc_notice {
  font-size: 2.7vw;
  line-height: 1.5;
  text-align: justify;
}

.productItem_img {
  margin-top: 7%;
}

.productItem_btn {
  background-color: #F5F5F5;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 4.2vw;
  font-weight: 600;
  line-height: 1.5;
  padding: 8% 6%;
}

.productItem_btn::after {
  content: "";
  aspect-ratio: 15/17;
  background-image: url(../images/common/arrow_right_green.svg);
  background-size: cover;
  display: block;
  flex-shrink: 0;
  margin-left: 7%;
  width: 3%;
}

/* /page-templates/brandPage-menu.php */
.brandPage_menu_wrap {
  display: flex;
  flex-direction: column;
  gap: 5.4vw;
}

.brandPage_menu_item {
  aspect-ratio: 67/10;
  background-color: #fff;
  border: 0.3vw solid #000;
  border-radius: 2.7vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: "Inter", sans-serif;
  font-size: 5.3vw;
  font-weight: 500;
  letter-spacing: 0.06em;
  line-height: 1.5;
  padding: 3% 6.6% 3% 5.1%;
}

.brandPage_menu_item::after {
  content: "";
  aspect-ratio: 15/48;
  background-image: url(../images/common/arrowLine_bottom_blandSite_black.svg);
  background-size: cover;
  display: block;
  flex-shrink: 0;
  margin-left: 7%;
  width: 2.6%;
}

/* /page-templates/brandPage-whatsFarmind.php */
/* blandWhatsFarmind */
.section_blandWhatsFarmind {
